<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right">
      <button class="btn btn-outline-primary btn-sm" @click="openModal(true)">增加新的優惠券</button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th>優惠券名稱</th>
          <th class="text-right" width="110">折扣數</th>
          <th width="140">折扣碼</th>
          <th width="100">到期日</th>
          <th width="85">是否啟用</th>
          <th width="130">修改</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in coupons" :key="item.id">
          <td>{{ item.title }}</td>
          <td class="text-right">{{ item.percent }} %</td>
          <td>{{ item.code }}</td>
          <td>{{ item.due_date }}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success">啟用</span>
            <span v-else class="text-danger">未啟用</span>
          </td>
          <td>
            <button class="btn btn-outline-info btn-sm" @click="openModal(false, item, '編輯')">編輯</button>
            <button class="btn btn-outline-danger btn-sm" @click="openDelModal(item)">刪除</button>
          </td>
        </tr>
      </tbody>
    </table>
    <pagination :pagination="pagination" @changePage="getCoupons"></pagination>
    <!-- couponModal start -->
    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="couponModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="couponModalLabel">
              <span>{{ purpose }}優惠券</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="title">優惠券標題</label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    placeholder="請輸入優惠券標題"
                    v-model="tempCoupon.title"
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="code">折扣碼本人</label>
                  <input
                    type="text"
                    class="form-control"
                    id="code"
                    placeholder="請輸入折扣碼"
                    v-model="tempCoupon.code"
                  />
                </div>
              </div>
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="percent">折扣</label>
                  <input
                    type="number"
                    class="form-control"
                    id="percent"
                    placeholder="請輸入折扣"
                    v-model="tempCoupon.percent"
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="due_date">到期日</label>
                  <input
                    type="date"
                    class="form-control"
                    id="due_date"
                    placeholder="請輸入到期日"
                    v-model="tempCoupon.due_date"
                  />
                </div>
              </div>
            </div>
            <hr />
            <div class="form-group">
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="checkbox"
                  id="is_enabled"
                  v-model="tempCoupon.is_enabled"
                  :true-value="1"
                  :false-value="0"
                />
                <label class="form-check-label" for="is_enabled">是否啟用</label>
              </div>
            </div>
          </div>

          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="updateCoupon">確認</button>
          </div>
        </div>
      </div>
    </div>
    <!-- couponModal end -->
    <!-- delCouponModal start -->
    <div
      class="modal fade"
      id="delCouponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="delCouponModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="delCouponModalLabel">
              <span>刪除優惠券</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            是否刪除
            <strong class="text-danger">{{ tempCoupon.title }}</strong>
            商品（刪除後將無法恢復）。
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-danger" @click="delCoupon()">確認刪除</button>
          </div>
        </div>
      </div>
    </div>
    <!-- delCouponModal end -->
  </div>
</template>

<script>
import $ from 'jquery';
import pagination from '../Pagination';
export default {
  data() {
    return {
      coupons: [],
      pagination: {},
      tempCoupon: {},
      isNew: false,
      isLoading: false,
      purpose: '增加新的',
    };
  },
   components: {
    pagination,
  },
  methods: {
    getCoupons(page = 1) {
      const vm = this;
      vm.isLoading = true;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      this.$http.get(api).then((response) => {
        vm.coupons = response.data.coupons;
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
      });
    },
    openModal(isNew, item, purpose) {
      if (purpose) {
        this.purpose = purpose;
      } else {
        this.purpose = '增加新的';
      }
      if (isNew) {
        this.tempCoupon = {};
        this.isNew = true;
      } else {
        this.tempCoupon = Object.assign({}, item);
        this.isNew = false;
      }
      $('#couponModal').modal('show');
    },
    updateCoupon() {
      const vm = this;
      let httpMethod = 'post';
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = 'put';
      }
      this.$http[httpMethod](api, { data: vm.tempCoupon }).then((response) => {
        if (response.data.success) {
          console.log(httpMethod);
          if (httpMethod === 'post') {
            this.$bus.$emit('message:push', '新增優惠券成功', 'success');
          } else if (httpMethod === 'put') {
            this.$bus.$emit('message:push', '編輯優惠券成功', 'success');
          }
          $('#couponModal').modal('hide');
          vm.getCoupons();
        } else {
          if (httpMethod === 'post') {
            this.$bus.$emit('message:push', '新增優惠券失敗', 'danger');
          } else if (httpMethod === 'put') {
            this.$bus.$emit('message:push', '編輯優惠券失敗', 'danger');
          }
          $('#couponModal').modal('hide');
          vm.getCoupons();
        }
      });
    },
    openDelModal(item) {
      this.tempCoupon = Object.assign({}, item);
      $('#delCouponModal').modal('show');
    },
    delCoupon() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      this.$http.delete(api).then((response) => {
        console.log(response.data);
        if (response.data.success) {
          this.$bus.$emit('message:push', '刪除優惠券成功', 'success');
          $('#delCouponModal').modal('hide');
          this.getCoupons();
        } else {
          this.$bus.$emit('message:push', '刪除優惠券失敗', 'danger');
          $('#delCouponModal').modal('hide');
          this.getCoupons();
        }
      });
    },
  },
  created() {
    this.getCoupons();
  },
};
</script>


