<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <table class="table mt-4">
      <thead>
        <tr>
          <th>購買時間</th>
          <th>Email</th>
          <th>購買品項</th>
          <th class="text-right">應付金額</th>
          <th>是否付款</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in orders" :key="item.id">
          <td>{{ item.create_at | formatDate }}</td>
          <td>{{ item.user.email }}</td>
          <td>
            <ul>
              <li v-for="good in item.products" :key="good.id">
                <p>{{ good.product.title }}：{{ good.qty }}{{ good.product.unit }}</p>
              </li>
            </ul>
          </td>
          <td class="text-right">{{ item.total | currency }}</td>
          <td>
            <span v-if="item.is_paid" class="text-success">已付款</span>
            <span v-else class="text-danger">未付款</span>
          </td>
        </tr>
      </tbody>
    </table>
    <pagination :pagination="pagination" @changePage="getOrders"></pagination>
  </div>
</template>

<script>
import moment from '../../../node_modules/moment';
import pagination from '../Pagination';
moment.locale('zh-tw');
export default {
  data() {
    return {
      isLoading: false,
      orders: {},
      pagination: {},
    };
  },
  components: {
    pagination,
    moment,
  },
  methods: {
    getOrders(page) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/orders?page=${page}`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        console.log(response.data);
        vm.orders = response.data.orders;
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
      });
    },
  },
  created() {
    this.getOrders();
  },
  filters: {
    formatDate(value) {
      return moment(value * 1000).format('Y/MM/DD, HH:mm:ss');
    },
  },
  
};
</script>


