// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
// 讀取中效果套件
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";
import "bootstrap";
import VeeValidate from "vee-validate";
import zhTW from "vee-validate/dist/locale/zh_TW"; //引入中文包，提示資訊可以以中文形式顯示
import VueI18n from "vue-i18n";

import App from "./App";
import router from "./router";
import "./bus";
import currencyFilter from "./filters/currency";
import pagination from "./components/Pagination";
Vue.config.productionTip = false;
Vue.use(VueAxios, axios);
// VeeValidate.Validator.localize("zh_TW", zhTWValidate);
// Vue.use(VeeValidate);
Vue.use(VueI18n);
const i18n = new VueI18n({
  locale: "zhTW"
});
// 將元件以及相關設定檔從vee-validate導出
// import {
//   ValidationObserver, //整體form的驗證元件
//   ValidationProvider, //input驗證元件
//   extend, //擴充功能
//   localize, //語系設定
//   configure //設定檔
// } from "vee-validate";
// import TW from "vee-validate/dist/locale/zh_TW.json";
// import * as rules from "vee-validate/dist/rules"; //驗證規則化 導出

// // 將規則導出並且加到vee-validate的擴充裡
// Object.keys(rules).forEach(rule => {
//   extend(rule, rules[rule]);
// });

// localize("zh_TW", TW);

// Vue.component("ValidationObserver", ValidationObserver); //整體form的驗證元件
// Vue.component("ValidationProvider", ValidationProvider); //input驗證元件

// configure({
//   classes: {
//     valid: "is-valid",
//     invalid: "is-invalid"
//   }
// });

Vue.use(VeeValidate, {
  events: "input|blur", //為了讓使用者離開該欄位時觸發驗證
  i18n,
  dictionary: {
    zhTW
  }
});

// 啟用讀取中效果套件
Vue.component("Loading", Loading);
Vue.component("pagination", pagination);
Vue.filter("currency", currencyFilter);
// 加入withCredentials 自動存入前端application/COOKIE 每次發送API 自動帶往後端送
axios.defaults.withCredentials = true;
/* eslint-disable no-new */
new Vue({
  i18n,
  el: "#app",
  router,
  components: { App },
  template: "<App/>"
});

router.beforeEach((to, from, next) => {
  console.log("to", to, "from", from, "next", next);
  // ...
  if (to.meta.requiresAuth) {
    console.log("這裡需要驗證!");
    const api = `${process.env.APIPATH}/api/user/check`;
    const vm = this;
    axios.post(api).then(response => {
      //   console.log(response.data);
      if (response.data.success) {
        next();
      } else {
        next({
          path: "/login"
        });
      }
    });
  } else {
    // console.log("跳");
    next();
  }
});



